import React from 'react';
import { events } from '../seed';

class EventListItem extends React.Component {
    render () {
        let e = this.props.e;
        return (
            <li className="event-item d-flex">
                <div className="e-left d-flex">
                    <div className="e-img-container" onClick={this.props.showModal}>
                        <img src={e.img} alt=""/>
                    </div>
                    <div className="e-meta" onClick={this.props.showModal}>
                        <div className="e-title-container">
                            <span className="e-title">{e.title}</span>
                            <span className="badge badge-warning">{e.type}</span> 
                        </div>
                        <div className="e-location">
                        <i className="fas fa-map-marker-alt"></i> {e.address}
                        </div>
                        <div className="e-dt">
                            <span><i className="far fa-calendar-alt"></i> {e.date}</span> {e.time !== "" ? <span><i className="far fa-clock"></i> {e.time}</span> : null}
                        </div>
                        <div className="e-stat">
                            <span>{e.user_count}/{e.people_count} People</span> &middot; <span>${e.price} each</span>
                        </div>
                    </div>
                </div>
                <div className="e-atn">
                    <button className="btn btn-info" onClick={this.props.onClickJoin.bind(this, e.id)}>Join</button>
                </div>
            </li>
        )
    } 
}

export default EventListItem;