import React from 'react';
import User from '../components/user';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
// seed
import {users, events} from '../seed';

class ModalCmpnt extends React.Component {
    getEvent (id) {
        return events[id];
    }
    render () {
        let eventId = this.props.eventId;
        let event = this.getEvent(eventId);
        return (
                <Modal 
                    show={this.props.show} 
                    onHide={this.props.handleClose}
                    centered>
                    <Modal.Header closeButton>
                        <Modal.Title>
                            <div className="e-title-container">
                                <span className="e-title">{event.title}</span>
                                <span className="badge badge-warning">{event.type}</span> 
                            </div>
                            <div className="d-flex">
                                <div className="e-img-container" onClick={this.props.handleShow}>
                                    <img src={event.img} alt=""/>
                                </div>
                                <div className="e-meta" onClick={this.props.handleShow}>
                                    <div className="e-location">
                                        <i className="fas fa-map-marker-alt"></i> 
                                        <span>{event.address}</span>
                                    </div>
                                    <div className="e-dt">
                                        <span><i className="far fa-calendar-alt"></i> {event.date}</span> {event.time && <span><i className="far fa-clock"></i> {event.time}</span>}
                                    </div>
                                    <div className="e-stat">
                                        <span>{event.user_count}/{event.people_count} People</span> &middot; <span>${event.price} each</span>
                                    </div>
                                </div>
                            </div>
                        </Modal.Title>
                    </Modal.Header>

                    <Modal.Body>
                        <div className="subtitle">Description</div>
                        <p>{event.description}</p>
                        <div className="subtitle subtitle-user">Participants</div>
                        <ul className="user-list">
                            {users.map((u, i) => <User u={u} key={i}/>)}
                        </ul>
                    </Modal.Body>

                    <Modal.Footer>
                        <Button variant="info" onClick={this.props.onHandleJoin.bind(this, event.id)}>Join</Button>
                    </Modal.Footer>
                </Modal>
        )
    } 
}

export default ModalCmpnt;