import React from 'react';
// asset
import m_sack from '../assets/m_sack.svg';
// layout
import Frame from '../layouts/frame';
// components
import User from '../components/user';
import MsgItem from '../components/msg_item';
import AdItem from '../components/ad_item';
// seed
import {users, msgs, events} from '../seed';

class Chat extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            pageHeight: 1080,
            msgWidth: null,
            text: "",
            msgs: [
                msgs[0],
                msgs[1]
            ]
        }
        this.pageEl = React.createRef();
        this.msgContainer = React.createRef();
        this.msgInput = React.createRef();
        this.myConvo = React.createRef();
        this.msgInterac = []
    }
    getEvent (id) {
        return events[id];
    }
    componentDidMount() {
        window.addEventListener("resize", this.updateDimensions.bind(this))
        this.scrollToBottom();
        this.msgInput.current.focus();
        msgs.forEach((m, i) => {
            if (i > 1) {
                const t = setTimeout(() => {
                    this.appendMsg(m)
                }, 1000+2000*i)
                this.msgInterac.push(t)
            }
        })
    }
    appendMsg (m) {
        let x = this.state.msgs;
        x.push(m)
        this.setState({
            msgs: x
        }, () => this.scrollToBottom());
    }
    updateDimensions () {
        this.setState({pageHeight: window.innerHeight-56})
    }
    componentWillMount () {
        this.updateDimensions();
    }
    componentWillUnmount () {
        window.removeEventListener("resize", this.updateDimensions);
        this.msgInterac.forEach(t => clearTimeout(t))
    }
    onType (e) {
            this.setState({text: e.target.value})
    }
    onKeyDown (e) {
        if (e.keyCode == 13 && this.state.text !== "") {
            e.preventDefault();
            let m = this.state.msgs;
            m.push({userId: 0, text: this.state.text})
            this.setState({msgs: m, text: ""}, () => this.scrollToBottom())
        }
    }
    scrollToBottom () {
        let myConvo = this.msgContainer;
        myConvo.current.scrollTop = myConvo.current.scrollHeight - myConvo.current.clientHeight;
    }
    render() {
        let myId = 0;
        let eventId = this.props.match.params.id;
        console.log('match', eventId)
        let e = this.getEvent(eventId);
        return (
            <Frame>
                <div className="row">
                    <div className="col-md-4 chat-info" ref={this.pageEl} style={{height: `${this.state.pageHeight}px`}}>
                        <div className="e-img-container" onClick={this.props.showModal}>
                            <img src={e.img} alt=""/>
                        </div>
                        <div className="e-meta" onClick={this.props.showModal}>
                            <div className="e-title-container">
                                <span className="e-title">{e.title}</span>
                                <span className="badge badge-warning">{e.type}</span> 
                            </div>
                            <div className="e-location">
                            <i className="fas fa-map-marker-alt"></i> {e.address}
                            </div>
                            <div className="e-dt">
                                <div><i className="far fa-calendar-alt"></i> {e.date}</div> 
                                {e.time && <div><i className="far fa-clock"></i> {e.time}</div>}
                            </div>
                            <div className="e-stat">
                                <div><i className="fas fa-users"></i> {e.user_count}/{e.people_count} People</div>
                                <div><i className="fas fa-dollar-sign"></i> {e.price} each</div>
                            </div>
                            <div className="e-pooled">
                                <div className="pooled-icon">Pooled $300</div>
                            </div>
                        </div>
                        <br/>
                        <div className="subtitle">Description</div>
                            <p>{e.description}</p>
                        <div className="subtitle">Participants</div>
                        <ul className="user-list">
                            {users.map((u, i) => <User u={u} key={i}/>)}
                        </ul>
                    </div>
                    <div className="col-md-8 msg-container">
                        <div className="msg-content msg" ref={this.msgContainer} style={{height: `${this.state.pageHeight-69}px`}}>
                            <ul className="msg-list">
                                {this.state.msgs.map((m, i) => {
                                    if (i == 4) {
                                        return (<div key={i}>
                                            <MsgItem msg={m} sender={myId == m.userId}/>
                                            <AdItem/>
                                            </div>)
                                    }
                                    return (<MsgItem msg={m} key={i} sender={myId == m.userId}/>)
                                })}
                            </ul>
                        </div>
                        <div className="msg-input">
                            <div className="input-group">
                                <input className="form-control" type="text" 
                                    ref={this.msgInput}
                                    value={this.state.text}
                                    onChange={this.onType.bind(this)}
                                    onKeyDown={this.onKeyDown.bind(this)}
                                    placeholder="Type your message"/>
                            </div>
                        </div>
                    </div>
                </div>
            </Frame>
        )
    } 
}

export default Chat;