import React from 'react';
import  AppContext  from '../context';

class Global extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            err: false,
            msg: null
        }
    }

    setMsg (m) {
        console.log("hello", m)
        this.setState({msg: m})
    }

    setErr () {
        this.setState({err: true})
    }

    clearMsg () {
        this.setState({msg: null, err: false})
    }

    render() {
        return (
            <AppContext.Provider
                value={{
                    err: this.state.err,
                    msg: this.state.msg,
                    setMsg: this.setMsg.bind(this),
                    setErr: this.setErr.bind(this),
                    clearMsg: this.clearMsg.bind(this)
                }}>
                {this.props.children}
            </AppContext.Provider>

        )
    }

}

export default Global;