import React from 'react';

class User extends React.Component {
    render () {
        let u = this.props.u
        if (u) {
            return (
                <li className="user">
                    <div className="user-img">
                        <img src={u.img} alt=""/>
                    </div>
                    <div className="user-name">
                        {u.firstName} {u.lastName} {u.organizer && <span className="badge badge-danger">Organizer</span>}
                    </div>
                </li>
            )
        } else {
            return null
        }
    } 
}

export default User;