import React from 'react';

class AdItem extends React.Component {
    render () {
        return (
            <li className="dv-item">
                <a href="https://www.walmart.ca/" target="_blank">
                <div className="dv-container">
                        <div className="dv-img">
                            <img src="https://img.grouponcdn.com/coupons/svWS786jtP7X3Y2JHsBTRQ/walmart_com-500x500/v1/t200x200.png" />
                        </div>
                        <p className="dv-text">
                            Bowling equipments 50% off! 
                            Hurry, ends tomorrow!
                        </p>
                </div>
                </a>
                
            </li>
        )
    } 
}

export default AdItem;