import React from 'react';
// seed
import {users} from '../seed';

class MsgItem extends React.Component {
    getUserById(id) {
        return users[id]
    }
    render () {
        let msg = this.props.msg
        if (this.props.sender) {
            return (
                <li className={`msg-item msg-u-sender`}>
                    <div className="msg-u-text">
                        {msg.text}
                    </div>
                    <div className="msg-u-img">
                        <img src={this.getUserById(msg.userId).img} alt="" width="50px" height="50px"/>
                    </div>
                </li>
            )
        }
        return (
            <li className={`msg-item`}>
                <div className="msg-u-img">
                    <img src={this.getUserById(msg.userId).img} alt="" width="50px" height="50px"/>
                </div>
                <div className="msg-u-text">
                    {msg.text}
                </div>
            </li>
        )
    } 
}

export default MsgItem;