import React from 'react';

class SearchInput extends React.Component {
    constructor (props) {
        super(props);
        this.state = {
            selected: 'All',
            q: this.props.initialSearch ? this.props.initialSearch : "",
            showSug: false
        }
        this.userInput = React.createRef();
    }

    onHandleSelected (e) {
        e.preventDefault();
        this.userInput.current.focus();
        this.setState({
            selected: e.target.text
        })
    }
    onChangeType (e) {
        this.setState({
            q: e.target.value,
            showSug: e.target.value !== ''
        })
    }
    onHandleKeyDown (e) {
        if (e.keyCode == 13 && !this.props.onSearchPage) {
            this.props.history.push(`/search?q=${this.state.q}`)
        } else if (e.keyCode == 13) {
            console.log("search", this.state.q)
            this.props.setQuery(this.state.q);
            this.setState({
                q: "",
                showSug: e.target.value !== ''
            })
        }
    }
    onClickSug (e) {
        this.setState({
            q: e.target.text,
            showSug: false
        })
    }
    renderFilter () {
        return (
            <form>
                <div className="form-row align-items-center">
                    <div className="col-sm-4 my-1">
                        <label className="sr-only" htmlFor="inlineFormInputGroupUsername">Price</label>
                        <div className="input-group">
                            <div className="input-group-prepend">
                            <div className="input-group-text">Price</div>
                            </div>
                            <input type="number" className="form-control" id="inlineFormInputGroupUsername"/>
                        </div>
                    </div>
                    <div className="col-sm-4 my-1">
                        <label className="sr-only" htmlFor="inlineFormInputGroupUsername">People</label>
                        <div className="input-group">
                            <div className="input-group-prepend">
                            <div className="input-group-text">People</div>
                            </div>
                            <input type="number" className="form-control" id="inlineFormInputGroupUsername"/>
                        </div>
                    </div>
                    <div className="col-sm-4 my-1">
                        <label className="sr-only" htmlFor="inlineFormInputGroupUsername">Date</label>
                        <div className="input-group">
                            <div className="input-group-prepend">
                            <div className="input-group-text">Date</div>
                            </div>
                            <input type="date" className="form-control" id="inlineFormInputGroupUsername"/>
                        </div>
                    </div>
                    <div className="col-sm-4 my-1">
                        <label className="sr-only" htmlFor="inlineFormInputGroupUsername">Time</label>
                        <div className="input-group">
                            <div className="input-group-prepend">
                            <div className="input-group-text">Time</div>
                            </div>
                            <input type="time" className="form-control" id="inlineFormInputGroupUsername"/>
                        </div>
                    </div>
                </div>
            </form>
        )
    }

    render () {
        return (
            <div className="row">
            <div className="col-md-12">
                <div className={`input-group mb-3 search-input${this.props.topMargin ? " search-input-mt" : ""}${this.props.bottomMargin ? " search-input-mb" : ""}`}>
                    <div className="input-group-prepend">
                        <button className="btn btn-outline-secondary dropdown-toggle" 
                            type="button" data-toggle="dropdown" aria-haspopup="true" 
                            aria-expanded="false">{this.state.selected}</button>
                        <div className="dropdown-menu">
                            <a className="dropdown-item"
                                onClick={this.onHandleSelected.bind(this)}>All</a>
                            <a className="dropdown-item"
                                onClick={this.onHandleSelected.bind(this)}>Roommate</a>
                            <a className="dropdown-item"
                                onClick={this.onHandleSelected.bind(this)}>Car pool</a>
                            <a className="dropdown-item"
                                onClick={this.onHandleSelected.bind(this)}>Event</a>
                        </div>
                    </div>
                    <div className="input-group-main">
                        <input type="text" className="form-control" 
                            value={this.state.q}
                            onChange={this.onChangeType.bind(this)}
                            onKeyDown={this.onHandleKeyDown.bind(this)}
                            ref={this.userInput}
                            aria-label="Search" placeholder="Search location"/>
                        {/* AUTO SUGGESTION */}
                        {/* <div className={this.state.showSug ? "dropdown-menu show-sug" : "dropdown-menu"}>
                            
                            <a className="dropdown-item" href="#" onClick={this.onClickSug.bind(this)}>Action</a>
                            <a className="dropdown-item" href="#" onClick={this.onClickSug.bind(this)}>Another action</a>
                        </div> */}
                    </div>
                </div>

                {/* filter */}
                {this.props.showFilter ? this.renderFilter() : null}
                {this.props.onSearchPage ? <button className="btn btn-info btn-search" onClick={this.props.setQuery.bind(this, this.state.q)}>Search</button> : null}
            </div>
            </div>
        )
    } 
}

export default SearchInput;