import React from 'react';
// layout
import Frame from '../layouts/frame';
// components
import SearchInput from '../components/search_input';
import EventListItem from '../components/event_list_item';
import ModalCmpnt from '../components/modal';
// context
import AppContext from '../context';
// seed
import {events} from '../seed';

class Search extends React.Component {
    static contextType = AppContext;

    constructor(props) {
        super(props);
        
        let params = new URLSearchParams(this.props.history.location.search);
        let q = params.get("q");

        this.state = {
            show: false,
            eventId: 0,
            q
        }
    }
    handleClose () {
        this.setState({ show: false })
    }
    handleShow (eventId) {
        this.setState({ show: true, eventId })
    }
    addMsg (m) {
        this.context.setMsg(m)
    }
    onHandleJoin (eventId) {
        this.addMsg("You have joined the event!")
        this.setState({ show: false })
        this.props.history.push(`/event/${eventId}`);
    }
    setQuery (q) {
        console.log('set', q)
        this.setState({q: q})
    }
    
    renderList () {
        return (
            <ul className="event-list">
                {events.map((e, i) => <EventListItem key={i} e={e} showModal={this.handleShow.bind(this, e.id)} onClickJoin={this.onHandleJoin.bind(this)}/>)}
            </ul>
        )
    }
    render () {
        return (
            <Frame>
                <SearchInput topMargin showFilter onSearchPage setQuery={this.setQuery.bind(this)} initialSearch={this.state.q}/>
                <div className="row section">
                    <div className="col-md-12">
                        <h4 className="section-title">Results for "<span>{this.state.q}</span>"</h4>
                        {this.renderList()}
                    </div>
                </div>
                
                <ModalCmpnt 
                    eventId={this.state.eventId}
                    handleClose={this.handleClose.bind(this)}
                    handleShow={this.handleShow.bind(this)}
                    onHandleJoin={this.onHandleJoin.bind(this)}
                    show={this.state.show}
                />
            </Frame>
        )
    } 
}

export default Search;