import React from 'react';

import AppContext from '../context';

class Notification extends React.Component {
    static contextType = AppContext;

    constructor (props) {
        super(props);
        this.state = {
            show: false
        }
        // this.Timeout = this.clear()
        this.timeouts = []
    }
    timer () {
        this.setState({show: false});
        this.context.clearMsg()
    }
    componentWillUnmount () {
        this.timeouts.forEach(i => clearTimeout(i));
    }
    clear () {
        let i = setTimeout(this.timer.bind(this), 8000)
        this.timeouts.push(i)
    }
    componentDidMount () {
        if (this.context.msg) {
            this.clear()
        }
    }

    render () {
        return (
            <AppContext.Consumer>
            {context => (<div className={context.msg ? "notif notif-show" : "notif"}>
                <div className="notif-container d-flex">
                    <div className="notif-msg">
                        {this.props.warning ? <i className="fas fa-exclamation-circle"></i> : <i className="far fa-bell"></i>} {context.msg}

                    </div>
                    <div className="notif-dismiss" onClick={this.timer.bind(this)}>&times;</div>
                </div>
            </div>)}
            </AppContext.Consumer>
        )
    } 
}

export default Notification;