import React from 'react';

import Notification from '../components/notification';

class Frame extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            fixedBar: true
        }
    }

    render () {
        return (
                <div className="container" style={{marginTop: this.state.fixedBar && "56px"}}>
                    <Notification msg={this.state.msg} warning={this.state.err}/>
                    {this.props.children}
                </div>
        )
    } 
}

export default Frame;